.radial-progress {
  display: flex;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: conic-gradient(var(--progress-color) var(--progress), var(--border-color) 0deg);
  font-size: 0;
}

.radial-progress::after {
  content: '';
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  margin: 6px;
  border-radius: 50%;
  background: var(--bg-block);
  font-size: 10px;
  text-align: center;
}