.Button {
  text-align: center;
  font-weight: 600;
  line-height: 1;

  border-radius: 10px;
}

.Button\/variant\/primary {
  color: var(--font-white);
  
  background-color: var(--main);
}

.Button\/variant\/secondary {
  background-color: blue;
}

.Button\/size\/l {
  padding-right: 18px;
  padding-left: 18px;
  
  font-size: 18px;
  line-height: 42px;
}

.Button\/size\/m {
  padding-right: 15px;
  padding-left: 15px;
  
  font-size: 16px;
  line-height: 36px;
}

.Button\/size\/m {
  padding-right: 13px;
  padding-left: 13px;

  font-size: 14px;
  line-height: 32px;
}