// Mixins
// __________________________________________________

@mixin size($width, $height) {
  height: $height;
  width: $width;
}

// Media queries 
// __________________________________________________
@mixin media-xl {
  @media screen and (max-width: breakpoint-get(xl)) {
    @content;
  }
}

@mixin media-ld {
  @media screen and (max-width: breakpoint-get(ld)) {
    @content;
  }
}

@mixin media-md {
  @media screen and (max-width: breakpoint-get(md)) {
    @content;
  }
}

@mixin media-sm {
  @media screen and (max-width: breakpoint-get(sm)) {
    @content;
  }
}

@mixin media-new-breackpoint ($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}